<style scoped>
    .layout{
        border: 1px solid #d7dde4;
        background: #f5f7f9;
        position: relative;
        border-radius: 4px;
        overflow: hidden;
    }
    .layout-copy{
        text-align: center;
        padding: 10px 0 20px;
        color: #9ea7b4;
    }
    .layout-menu-left{
        background: #464c5b;
    }
    .layout-header{
        height: 60px;
        background: #fff;
        box-shadow: 0 1px 1px rgba(0,0,0,.1);
    }
    .layout-logo-left{
        width: 90%;
        height: 50px;
        background: #5b6270;
        border-radius: 3px;
        margin: 15px auto;
        text-align: center;
        color: #fff;
    }
    .layout-logo-left h1{
        line-height: 50px;
    }
    .layout-ceiling-main a{
        color: #9ba7b5;
    }
    .layout-hide-text .layout-text{
        display: none;
    }
    .ivu-col{
        transition: width .2s ease-in-out;
    }
    #app .ivu-menu-item{
        padding: 0;
    }
    .nav-link-left{
        display: block;
        padding: 14px 24px;
    }
</style>
<template>
    <div id="app">
    <div class="layout" :class="{'layout-hide-text': spanLeft < 5}">
        <Row type="flex">
            <i-col :span="spanLeft" class="layout-menu-left">
                <Menu active-key="1" theme="dark" width="auto">
                    <div class="layout-logo-left"><h1>Clara</h1></div>
                    <Menu-item key="1">
                        <a class="nav-link-left" v-link="{ path: '/home'}">
                        <Icon type="ios-navigate" :size="iconSize"></Icon>
                        <span class="layout-text">Home</span></a>
                    </Menu-item>
                    <Menu-item key="2">
                        <a class="nav-link-left" v-link="{ path: '/skill'}">
                        <Icon type="ios-keypad" :size="iconSize"></Icon>
                        <span class="layout-text">Skills</span></a>
                    </Menu-item>
                    <Menu-item key="3">
                        <a class="nav-link-left" v-link="{ path: '/about'}">
                        <Icon type="ios-analytics" :size="iconSize"></Icon>
                        <span class="layout-text">About Me</span></a>
                    </Menu-item>
                </Menu>
            </i-col>
            <i-col :span="spanRight">
                <div class="layout-header">
                    <i-button type="text" @click="toggleClick">
                        <Icon type="navicon" size="32"></Icon>
                    </i-button>
                </div>
                <router-view></router-view>
                <div class="layout-copy">
                    2017 &copy; Created By Clara
                </div>
            </i-col>
        </Row>
    </div>
    </div>
</template>
<script>
  export default {
    data () {
      return {
        spanLeft: 5,
        spanRight: 19
      }
    },
    computed: {
      iconSize () {
        return this.spanLeft === 5 ? 14 : 24
      }
    },
    methods: {
      toggleClick () {
        if (this.spanLeft === 5) {
          this.spanLeft = 2
          this.spanRight = 22
        } else {
          this.spanLeft = 5
          this.spanRight = 19
        }
      }
    }
  }
</script>



