	module.exports = " <div class=layout-breadcrumb _v-0f921656=\"\"> <breadcrumb _v-0f921656=\"\"> <breadcrumb-item _v-0f921656=\"\">HOME</breadcrumb-item> </breadcrumb> </div> <div class=layout-content _v-0f921656=\"\"> <div class=layout-content-main _v-0f921656=\"\"> <h1 _v-0f921656=\"\">卷首语</h1> <div _v-0f921656=\"\"> <p _v-0f921656=\"\">梦想的执著，其实无关性别，无关年龄，无关岁月，重要的在于一个火热的心，不似青春胜似青春。</p> <p _v-0f921656=\"\">人生如一条大河，时而澎湃时而沉缓；又如一曲乐章，时而激昂时而低吟，但始终保持着一种前进的姿势。我们都是故事里的主角，一幕幕悲欢离合的剧情掌握在自己手中，由自己编辑，导演，剪辑。或许，我们哭过，我们累过，我们熬过，我们怨过，我们恨过，当太多的不容易汇聚，熔炼的必是点金精光。</p> <p _v-0f921656=\"\">如果存在轮回，我愿意一如既往的选择做一个追风筝的人。</p> <img src=http://www.lookmw.cn/uploads/160713/1-160G31AS63J.jpg _v-0f921656=\"\"> </div> </div> </div>";

